html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1,
h2,
h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

/** Media Queries **/
.container {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
}

.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #e8505b;
}

.header {
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  width: 30rem;
  margin-bottom: 3rem;
}
@media (min-width: 1024px) {
  .header__logo {
    margin-bottom: 0;
  }
}

.navbar__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .navbar__list {
    flex-direction: row;
  }
}
.navbar__link {
  color: #000;
  font-size: 1.9rem;
  font-weight: 700;
}
.navbar__link--register {
  background-color: #e8505b;
  font-size: 1.7rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.navbar__link--register:hover {
  cursor: pointer;
  background-color: #e22331;
}
@media (min-width: 768px) {
  .navbar__link {
    font-size: 1.35rem;
  }
}
@media (min-width: 1024px) {
  .navbar__link {
    font-size: 1.8rem;
  }
}

@media (min-width: 768px) {
  .section__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.section__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #e8505b;
  padding: 5rem;
}
.section__title {
  color: #FFFFFF;
  text-align: center;
}
.section__camp {
  margin-bottom: 1rem;
}
.section__camp label {
  display: block;
  color: #FFFFFF;
  margin: 1rem 0;
  font-weight: 700;
  text-transform: uppercase;
}
.section__camp input {
  width: 100%;
  padding: 1.3rem;
  border: none;
}
.section__camp input[type=submit] {
  background-color: #455a64;
  font-size: 1.7rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
  margin-top: 3rem;
}
.section__camp input[type=submit]:hover {
  cursor: pointer;
  background-color: #303f46;
}
.section__container-img {
  margin-top: 3rem;
  display: flex;
  justify-content: center;
}
.section__img {
  width: 30rem;
}
@media (min-width: 1024px) {
  .section__img {
    width: 100%;
  }
}

.section--steps {
  margin-top: 5rem;
}

.steps__wave {
  width: 100%;
  margin-right: -1px;
  margin-left: -1px;
}
.steps__content {
  background-color: #e8505b;
  margin-top: -1px;
}
.steps__title {
  color: #FFFFFF;
  text-align: center;
}
.steps__title span {
  display: block;
  font-size: 2.6rem;
}
@media (min-width: 768px) {
  .steps__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.steps__img {
  transition-property: transform;
  transition-duration: 0.3s;
}
.steps__img:hover {
  transform: rotate(-4deg) scale(1.2);
}

.testimonials {
  padding: 5rem;
}
.testimonials__title {
  text-align: center;
}
@media (min-width: 768px) {
  .testimonials__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(5, 1fr);
  }
}
.testimonials__user {
  background-color: #455a64;
  border-radius: 1rem;
  color: #FFFFFF;
  padding: 2rem;
  margin-bottom: 2rem;
}
.testimonials__user:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .testimonials__user {
    margin-bottom: 0;
  }
  .testimonials__user:nth-child(1) {
    grid-column: 1/4;
  }
  .testimonials__user:nth-child(3) {
    grid-column: 5/6;
    grid-row: 1/3;
  }
  .testimonials__user:nth-child(4) {
    grid-column: 1/3;
  }
  .testimonials__user:nth-child(5) {
    grid-column: 3/5;
  }
}
.testimonials__header {
  display: flex;
  align-items: center;
  gap: 2rem;
}
.testimonials__img {
  border-radius: 50%;
  flex-basis: 10rem;
  overflow: hidden;
}
.testimonials__author {
  margin: 0.5rem 0;
  color: #FFFFFF;
  font-weight: bold;
}
.testimonials__comment {
  padding-left: 2rem;
  margin: 2rem 0 0 0;
  position: relative;
  line-height: 1.5;
  font-size: 1.7rem;
}
.testimonials__comment::before {
  content: '"';
  position: absolute;
  opacity: 0.2;
  font-family: Georgia, "Times New Roman", Times, serif;
  font-size: 10rem;
  left: 0;
  top: -4rem;
}

.favorites {
  background-color: #eceeef;
  padding: 5rem 0;
}
.favorites__title {
  color: #455a64;
  text-align: center;
}
@media (min-width: 768px) {
  .favorites__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.favorites__content {
  background-color: #FFFFFF;
  border-radius: 1rem;
  box-shadow: 0px 0px 2.6rem -0.8rem #7d8b92;
  padding: 3rem;
  margin-bottom: 2rem;
}
.favorites__content:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .favorites__content {
    margin-bottom: 0;
    display: grid;
    grid-template-columns: 2fr 3fr;
    align-items: center;
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .favorites__content {
    grid-template-columns: 1fr 3fr;
    gap: 4rem;
  }
}
.favorites__img {
  max-width: 20rem;
  margin: 0 auto 3rem auto;
}
.favorites__information {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .favorites__information {
    align-items: flex-start;
  }
}
.favorites__name {
  margin: 1rem 0;
  color: #e8505b;
  font-size: 2.4rem;
}
.favorites__description {
  margin: 0.5rem 0;
  color: #455a64;
  font-size: 1.7rem;
  line-height: 1.5;
}

.dealers {
  padding: 5rem;
}
.dealers__title {
  color: #455a64;
  text-align: center;
}
@media (min-width: 768px) {
  .dealers__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.dealers__content {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
}
.dealers__paragraph {
  color: #455a64;
  font-size: 1.7rem;
  line-height: 1.5;
}
.dealers__link {
  width: auto;
  background-color: #e8505b;
  font-size: 1.7rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.dealers__link:hover {
  cursor: pointer;
  background-color: #e22331;
}

.download {
  background-color: #eceeef;
  padding: 5rem;
}
@media (min-width: 768px) {
  .download__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.download__img {
  width: 50%;
  margin: 0 auto 5rem auto;
  transition-property: transform;
  transition-duration: 0.3s;
}
.download__img:hover {
  transform: rotate(4deg) scale(1.1);
}
@media (min-width: 768px) {
  .download__img {
    margin-bottom: 0;
  }
}
.download__store {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.download__title {
  color: #455a64;
  margin-bottom: 1rem;
  text-align: center;
}
.download__paragraph {
  margin-bottom: 3rem;
  text-align: center;
}
.download__links {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
}
.download__link {
  transition-duration: 0.3s;
}
.download__link:hover {
  transform: scale(1.1);
}

.footer {
  background-color: #455a64;
  padding-top: 5rem;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.footer__box {
  margin-bottom: 5rem;
}
.footer__title {
  color: #FFFFFF;
  margin: 0;
  text-align: center;
}
@media (min-width: 768px) {
  .footer__title {
    text-align: left;
  }
}
.footer__paragraph {
  color: #FFFFFF;
  font-size: 1.7rem;
  line-height: 1.5;
  text-align: center;
}
@media (min-width: 768px) {
  .footer__paragraph {
    text-align: left;
  }
}
.footer__nav {
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
}
.footer__nav li {
  text-align: center;
}
@media (min-width: 768px) {
  .footer__nav li {
    text-align: left;
  }
}
.footer__link {
  color: #FFFFFF;
  font-size: 1.7rem;
  line-height: 1.5;
}
.footer__copyright {
  background-color: #e8505b;
  color: #FFFFFF;
  margin: 0;
  padding: 2rem;
  font-size: 1.7rem;
  text-align: center;
}